.profile__avatar {
  object-fit: cover;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  z-index: 3;
}

@media screen and (max-width: 900px) {
  .profile__avatar {
    justify-self: center;
  }
}

.profile__avatar:hover {
  cursor: pointer;
  opacity: 0.2;
  z-index: 1;
}
